@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Roboto",sans-serif;background:url(/cv/static/media/background.be8d2259.png);background-position:center}.logo,.rounded{margin:auto;display:block;-webkit-animation:fadein 2s;animation:fadein 2s}.logo{margin-top:100px}.rounded{margin-top:50px}h1,h2,p{font-weight:100;text-align:center;color:#fff;-webkit-animation:fadein 1s;animation:fadein 1s}h2{margin-bottom:40px}p{font-weight:normal;margin-left:10px;margin-right:10px}a{font-weight:bold;color:#fff}.bar-footer{position:fixed;bottom:0px}.rounded{margin:auto;margin-top:100px;display:block;width:150px;height:150px;border-radius:75px;-webkit-border-radius:75px;-moz-border-radius:75px;background:url(/cv/static/media/moi.b6021be0.png) no-repeat}.card{-webkit-animation:fadein 1s;animation:fadein 1s}i{font-size:20px}.item-text-wrap a{margin-top:20px}@keyframes fadein{from{opacity:0}to{opacity:1}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}
